.preloader-image {
  background: url("./assets/gif/preloader.gif") no-repeat;
}

body {
  width: 100vw;
  height: 100vh;
  background: url("./assets/images/background.webp") no-repeat;
  background-size: cover;
}

%message {
  font-family: Georgia, sans-serif;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 1px;
  color: rgb(105, 65, 217);
  background: none;
}

%active {
  transform: translateY(3px);
  box-shadow: 0 1px 0 rgb(56, 56, 250);
  color: blue;
}

.keyboard-input {
  @extend %message;
  font-size: 25px;
  color: blue;
  position: fixed;
  left: 122px;
  width: 746px;
  height: 350px;
  bottom: 400px;
  border-radius: 5px;
  resize: none;
  border: 5px rgb(86, 62, 119) solid;
  outline: none;
  background-color: rgb(164, 111, 238);
  opacity: 0.8;
}

.keyboard {
  position: fixed;
  left: 60px;
  bottom: 60px;
  gap: 10px;
  width: 870px;
  &__key {
    @extend %message;
    width: 52px;
    height: 52px;
    margin: 3px;
    border: 2px rgb(137, 54, 252) solid;
    border-radius: 10px;
    box-shadow: 0 4px rgb(137, 54, 252);
    background-color: #0c0c0c;
    opacity: 0.9;
    transition: 0.1s;

    &:hover {
      color: rgb(56, 56, 250);
      opacity: 1;
    }

    &:active {
      @extend %active;
    }

    &--wide {
      width: 110px;
    }

    &--extra-wide {
      width: 342px;
      letter-spacing: 5px;
    }
  }
}

.active {
  @extend %active;
  &-hold {
    @extend %active;
  }
}

.text {
  @extend %message;
  position: fixed;
  bottom: 780px;
  left: 340px;
  font-size: 20px;
  color: rgb(76, 0, 96);
  text-shadow: 1px 1px 2px rgb(76, 0, 96);
}
